import { Component, ElementRef, OnInit, ViewChild } from '@angular/core'
import RPPG from 'rppg/dist/lib/RPPG'
import { MeasurementMeanData } from 'rppg/dist/lib/RPPGSocket.types'
import { BUTTON_TYPES, NO_DATA } from 'src/consts'
import { translations } from 'src/consts/translations'
import { environment } from '../environments/environment'
import { DocserviceService } from '../docservice.service'
import { HttpClient } from '@angular/common/http'

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})

export class AppComponent implements OnInit {
  @ViewChild('videoElement') videoElement: ElementRef<HTMLVideoElement>
  private http: HttpClient
  
  canvasCtx: CanvasRenderingContext2D | null = null
  title = 'RPPG Demo'
  rppgInstance = null
  authToken = environment.AUTH_TOKEN
  averageFps = 0
  width = 0
  height = 0
  buttonType = BUTTON_TYPES.PRIMARY
  ready = false
  progress = 0
  bgrData: MeasurementMeanData = {
    bpm: 0,
    rr: 0,
    oxygen: 0,
    stressStatus: null,
    bloodPressureStatus: null,
  }
  signal: number[] = []
  interferenceWarning = false
  notificationType = null
  processing = false
  lang: 'en' | 'fr' = 'en'
  buttonText = this.BUTTON_VALUE.INIT
  status = this.SIGNAL_STATUS.INITIALIZING

constructor(private docservice: DocserviceService){

}
  
  get BUTTON_VALUE() {
    return translations[this.lang]['buttonValue']
  }

  get SIGNAL_STATUS() {
    return translations[this.lang]['signalStatus']
  }

  ngOnInit() {
    this.lang = 'fr'
    this.buttonText = this.BUTTON_VALUE.INIT
    this.status = this.SIGNAL_STATUS.INITIALIZING
  }

  ngAfterViewInit() {
    this.initRPPG()

    var urlstring = (window.location.href.replace('#',''))
    var urlsss = new URL(urlstring)
    
    this.appointmentID = urlsss.searchParams.get("AppointmentID")
    this.patientid = urlsss.searchParams.get("PatientID")
    this.TypeID = urlsss.searchParams.get("TypeID")

  }

  async initRPPG() {
    this.rppgInstance = new RPPG({
      onFrame: (data) => {
        this.averageFps = data.averageFps
        if (data.averageFps < 12) {
          this.notificationType = 'LOW_FPS_WARNING'
        } else if (!this.interferenceWarning) {
          this.notificationType = null
        }
       
      },
      onMeasurementMeanData: (data) => {
        this.bgrData = this.normalizeBGRData(data)
        this.checkIsAllDataCalculated()
       
      },
      onMeasurementStatus: (data) => {},
      onMeasurementProgress: (data) => {
        this.progress = data.progressPercent
      },
      onSignalQuality: ({snr}) => {
        if (snr < 2 && snr > -10) {
          this.status = this.SIGNAL_STATUS.WEAK_SIGNAL
        } else if (snr === -100) {
          this.status = this.SIGNAL_STATUS.INITIALIZING
        } else {
          this.status = this.SIGNAL_STATUS.MEASURING
        }
      },
      onMeasurementSignal: ({ signal }) => {
        if (this.processing) {
          this.signal = signal
          console.log(this.signal)
        }
      },
      onInterferenceWarning: (InterferenceWarning) => {
        this.stopHandler()
        this.interferenceWarning = true
        this.notificationType = 'INTERFERENCE_WARNING'
      }
    })

    await this.initCamera()
    await this.initTracker()
    await this.initSocket()
  }

  initCamera() {
    return this.rppgInstance.initCamera({
      width: 640,
      height: 480,
      videoElement: this.videoElement.nativeElement,
      onSuccess: (event) => {
      
        this.width = event.width
        this.height = event.height
      },
    })
  }

  initTracker() {
    return this.rppgInstance.initTracker()
  }

  initSocket() {
    return this.rppgInstance.initSocket({
      url: environment.SOCKET_URL,
      authToken: this.authToken,
      onMessage: (messageType, data) => {
       
      },
      onConnect: () => {
      
        this.ready = true
        this.buttonText = this.BUTTON_VALUE.START
        this.buttonType = BUTTON_TYPES.PRIMARY
      },
      onError: (event) => {

        this.ready = false
        this.buttonText = this.BUTTON_VALUE.INIT
        this.buttonType = BUTTON_TYPES.PRIMARY
      },
      onClose: (event) => {

        this.ready = false
        this.buttonText = this.BUTTON_VALUE.START
        this.buttonType = BUTTON_TYPES.PRIMARY
      }
    })
  }

  async onClickButtonHandler() {
    if (this.rppgInstance.processing) {
      this.stopHandler()
    } else {
      this.startHandler()
    }
  }

  async stopHandler() {
    this.rppgInstance.stop()
    this.processing = false
    this.interferenceWarning = false
    this.signal = []
    this.buttonText = this.BUTTON_VALUE.INIT
    this.buttonType = BUTTON_TYPES.PRIMARY
    await this.initSocket()
  }

  async startHandler() {
    this.notificationType = null
    this.averageFps = 0
    this.status = this.SIGNAL_STATUS.INITIALIZING
    this.progress = 0
    this.bgrData = {
      bpm: 0,
      rr: 0,
      oxygen: 0,
      stressStatus: null,
      bloodPressureStatus: null,
    }
    this.signal = []
    this.interferenceWarning = false
    this.rppgInstance.start()
    this.processing = true
    this.buttonText = this.BUTTON_VALUE.STOP
    this.buttonType = BUTTON_TYPES.SECONDARY

  }

  normalizeBGRData(bgrData: MeasurementMeanData): MeasurementMeanData {
    const {
      stressStatus,
      bloodPressureStatus,
    } = bgrData
    return {
      ...bgrData,
      stressStatus: (stressStatus === null || String(stressStatus) === NO_DATA) ? null : stressStatus,
      bloodPressureStatus: bloodPressureStatus === null || String(bloodPressureStatus) === NO_DATA ? null : bloodPressureStatus
    }
  }

  checkIsAllDataCalculated(): void {
    const {
      bpm,
      rr,
      oxygen,
      stressStatus,
      bloodPressureStatus
    } = this.bgrData
    const isAllDataCalculated = Boolean(bpm && rr && oxygen && stressStatus && bloodPressureStatus)
    if (isAllDataCalculated) {
      console.log('Calculated bgrData:', this.bgrData)
      alert('All data was calculated!')
      this.stopHandler()
      
    }
  }



  appointmentID: any
  patientid: any
  TypeID: any

}
